    monospace;
}

svg {
	vertical-align: text-bottom;
}
.App {
  text-align: center;
  font-size: calc(10px + 2vmin); }

h1 {
  margin: 2vh;
  font-size: calc(10px + 4vmin);
  z-index: 100; }

h2 {
  margin: 2vh;
  font-size: calc(10px + 3vmin);
  z-index: 100; }

h3 {
  font-size: calc(10px + 2vmin);
  margin: 1vh;
  font-weight: 700;
  border-color: black;
  border-left: 3px solid;
  border-right: 3px solid;
  border-bottom: 3px solid;
  border-radius: 8px;
  padding: 10px;
  position: relative;
  min-width: 12vw; }
  h3::after {
    content: "";
    position: absolute;
    right: -3px;
    z-index: 100;
    top: 0;
    width: 7px;
    height: 80%;
    background: #ecf0f1; }
  h3::before {
    content: "";
    position: absolute;
    left: -3px;
    z-index: 100;
    top: 0;
    width: 7px;
    height: 80%;
    background: #ecf0f1; }

h4 {
  font-weight: 700;
  margin: 1vmin; }

h5 {
  font-style: italic;
  font-size: calc(10px + .6vmin);
  margin: 1vmin; }

p {
  line-height: 2;
  font-size: calc(10px + .75vmin); }

button {
  border-radius: 8px;
  background-color: #2c3e50;
  color: white;
  border: 1px solid #2c3e50;
  padding: 10px;
  font-size: calc(10px + 1vmin);
  transition: all 0.75s ease 0s; }
  button:hover {
    background-color: white;
    color: #2c3e50;
    transition: all 0.5s ease 0s; }
  button:focus, button:active {
    outline: none; }

.margin {
  margin: 0 0 2vh 0; }

.light-border::after {
  background: white; }

.light-border::before {
  background: white; }

.medium-border::after {
  background: #2c3e50; }

.medium-border::before {
  background: #2c3e50; }

.title-space {
  margin-top: -3vh;
  margin-bottom: 3vh; }

.picture {
  z-index: 100;
  border-radius: 50%;
  height: 40vmin;
  border: 2px solid #F5F5F5; }

.relative {
  position: relative;
  width: 4vmax;
  margin: 2vmax; }

.link-image {
  z-index: 100;
  height: 2.75vmax;
  transition: all 500ms ease 0ms;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  .link-image:hover, .link-image:focus, .link-image:active {
    transition: all 500ms ease 0ms;
    height: 3.5vmax;
    min-width: 30px;
    min-height: 30px;
    cursor: pointer; }

.linkedin:hover, .linkedin:focus, .linkedin:active {
